import React, { useState } from "react";
import {
  Box,
  Typography,
  TextField,
  Button,
  Paper,
  Alert,
} from "@mui/material";
import { useAuth } from "../../context/AuthContext";
import { fetchChangePassword } from "../../actions/CallApi/LoginFetch";

const UserInforPage = () => {
  const { user, callApiWithToken } = useAuth();

  const [viewMode, setViewMode] = useState("info");
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleChangePassword = async () => {
    setMessage(null);

    if (!oldPassword || !newPassword || !confirmPassword) {
      setMessage({ type: "error", text: "全ての項目を入力してください。" });
      return;
    }

    if (newPassword !== confirmPassword) {
      setMessage({ type: "error", text: "新しいパスワードが一致しません。" });
      return;
    }

    setLoading(true);
    try {
      await callApiWithToken(fetchChangePassword, {
        new_password: newPassword,
        old_password: oldPassword,
      });
      setMessage({ type: "success", text: "パスワードが正常に変更されました。" });
      setOldPassword("");
      setNewPassword("");
      setConfirmPassword("");
    } catch (err) {
      setMessage({
        type: "error",
        text:
          err?.response?.data?.message ||
          err?.message ||
          "パスワードの変更に失敗しました。",
      });
    } finally {
      setLoading(false);
    }
  };

  if (!user) {
    return <Typography>ユーザー情報が見つかりません。</Typography>;
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        {viewMode === "info" ? "個人情報" : "パスワード変更"}
      </Typography>

      <Paper sx={{ p: 3, maxWidth: 500 }}>
        {viewMode === "info" ? (
          <>
            <TextField
              label="従業員番号"
              value={user.id || ""}
              fullWidth
              margin="normal"
              disabled
            />
            <TextField
              label="名前"
              value={user.name || ""}
              fullWidth
              margin="normal"
              disabled
            />
            <TextField
              label="メールアドレス"
              value={user.email || ""}
              fullWidth
              margin="normal"
              disabled
            />
            <TextField
              label="電話番号"
              value={user.phone || ""}
              fullWidth
              margin="normal"
              disabled
            />
            <Box mt={2} textAlign="right">
              <Button
                variant="outlined"
                onClick={() => setViewMode("password")}
              >
                パスワード変更
              </Button>
            </Box>
          </>
        ) : (
          <>
            {message && (
              <Alert
                severity={message.type}
                sx={{ mb: 2 }}
                onClose={() => setMessage(null)}
              >
                {message.text}
              </Alert>
            )}

            <TextField
              label="以前のパスワード"
              type="password"
              fullWidth
              margin="normal"
              value={oldPassword}
              onChange={(e) => setOldPassword(e.target.value)}
            />
            <TextField
              label="新しいパスワード"
              type="password"
              fullWidth
              margin="normal"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
            />
            <TextField
              label="新しいパスワード（確認）"
              type="password"
              fullWidth
              margin="normal"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />

            <Box mt={2} display="flex" justifyContent="space-between">
              <Button
                variant="contained"
                color="primary"
                onClick={handleChangePassword}
                disabled={loading}
              >
                {loading ? "変更中..." : "変更"}
              </Button>
              <Button variant="outlined" onClick={() => setViewMode("info")}>
                戻る
              </Button>
            </Box>
          </>
        )}
      </Paper>
    </Box>
  );
};

export default UserInforPage;
